.notificationContainer {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .notificationTitle {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .notificationList {
    list-style-type: none;
    padding: 0;
  }
  
  .notificationItem {
    padding: 10px;
    margin-bottom: 10px;
    background-color: #f1f1f1;
    border-radius: 5px;
  }
  
  .notificationItem.unread {
    background-color: #ffddc1; /* Cor para notificações não lidas */
  }
  
  .notificationItem strong {
    font-size: 1.1rem;
  }
  
  .noNotifications {
    font-style: italic;
    color: #999;
  }
  